import {Component, Inject, OnDestroy, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Sheet700Service} from '../sheet700.service';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { resolveSrv } from 'dns';
import { NotifierService } from 'src/app/notifier.service';

@Component({
  selector: 'app-dialog',
  templateUrl: './dialog.component.html',
  styleUrls: ['./dialog.component.css']
})
export class DialogComponent implements OnInit, OnDestroy {
  picker1: any;
  picker2: any;
  mdfir700Form: FormGroup;
  actionsBtn = 'Save';

  constructor(private formBuilder: FormBuilder,
              private sheet700Service: Sheet700Service,
    @Inject(MAT_DIALOG_DATA) public editData: any,
    private notifierService : NotifierService,
              // tslint:disable-next-line:no-shadowed-variable
              private MatDialogRef: MatDialogRef<DialogComponent>) { }

  ngOnInit(): void {
    this.mdfir700Form = this.formBuilder.group({
      code: ['', Validators.required],
     description: ['', Validators.required],
     outstanding_at_beginning_of_month: ['', Validators.required],
       outstanding_at_end_of_month: ['', Validators.required],
       average_opening_and_closing_balance: ['', Validators.required],
      paid_interest_during_the_month: ['', Validators.required],

    });
    if (this.editData){
      this.actionsBtn = 'Update';
      // tslint:disable-next-line:no-unused-expression
      this.mdfir700Form.controls.code.setValue(this.editData.code);
      this.mdfir700Form.controls.description.setValue(this.editData.description);
      this.mdfir700Form.controls.outstanding_at_beginning_of_month.setValue(this.editData.outstanding_at_beginning_of_month);
      this.mdfir700Form.controls.outstanding_at_end_of_month.setValue(this.editData. outstanding_at_end_of_month);
      this.mdfir700Form.controls.average_opening_and_closing_balance.setValue(this.editData. average_opening_and_closing_balance);
      this.mdfir700Form.controls.paid_interest_during_the_month.setValue(this.editData.paid_interest_during_the_month);
     
    }
  }

  ngOnDestroy(): void {
  }

  addDetails(): void {
    if (!this.editData){
      if (this.mdfir700Form.valid) {
        this.sheet700Service.postMDFIR700Data(this.mdfir700Form.value).subscribe(
          {
            next: (res) => {

   this.notifierService.showNotification('MDFIR700 updated successfully', 'ok', 'success');
              this.mdfir700Form.reset();
              this.MatDialogRef.close('save');
            },
            error: () => {
             this.notifierService.showNotification('Unable to add new Row', 'OK', 'error');
            }}
        );
      }
    } else {
      this.putDetails();
  }
    // tslint:disable-next-line:typedef
    console.log(this.mdfir700Form.value);
}

  // tslint:disable-next-line:typedef
  private putDetails(): void {
    this.sheet700Service.putMDFIR700Data(this.editData.id, this.mdfir700Form.value).subscribe({
      next: (response) => {
       this.notifierService.showNotification('MDFIR700 updated successfully', 'ok', 'success');
        this.mdfir700Form.reset();
        this.MatDialogRef.close('update');
      },
      error: () => {
                  this.notifierService.showNotification('Error updating Row', 'OK', 'error');
      }
    });
  }
  }

